<script>
export default {
    name: "AppFormDialog",
    props: ['value', 'valueFormat', 'portion', 'dialog'],  
    data() { 
        return {
            loader: null,
            loading: false,            
        }
    },

    computed: {
        valueReceive: function () {
            // return parseInt(this.value) - this.$loanRates
            return parseFloat(this.valueFormat.replace( /,/g, '')) - this.$loanRates
        }
    },  

    methods: { 
        close: function() {
            this.$emit('closeModal');
        },

        // Método para envio do form, pronto para implementação.
        // Chama a prop http da instância Vue que recebe axios. 'Ver constantes em main.js'
        send: function() {
            /*axios.post('/api/submit', {
                value: this.value,
                ...
            })*/
        }
    },
    
    watch: {
        loader: function() {
            const l = this.loader
            this[l] = !this[l]

            setTimeout(() => {
                this[l] = false,
                this.$router.push("/sucesso");
            }, 3000)

            this.loader = null
        }
    }    
}
</script>
