import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Vuetify from 'vuetify'
import '../node_modules/vuetify/dist/vuetify.min.css'
import Vue2Filters from 'vue2-filters'
import money from 'v-money'

Vue.use(money, {precision: 4})


Vue.use(Vue2Filters)

Vue.filter('currencyReal', function (value, symbol) {
	let currency = Vue.filter('currency')
	symbol = symbol != null ? symbol : 'R$ '
	return currency(value, symbol, 2, { thousandsSeparator: '.', decimalSeparator: ',' })
})
  
/* Constants */ 
Vue.prototype.$loanRates = 150.00
Vue.prototype.$intRate = 2.4
Vue.prototype.$iof = 3
Vue.prototype.$iofPerDay = 0.0082
// Vue.prototype.$http = axios

Vue.config.productionTip = false

Vue.use(Vuetify)

new Vue({
	router,
	render: h => h(App)
}).$mount('#app')